
.top-container {
	height: 100%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}
.header-container {
	height: 50px;
	background-color: #555;
	display: flex;
}
.header-item {
	width: 100px;
	text-align: center;
}
.header-item>span {
	color: white;
	line-height: 50px;
}
.header-item>span.active {
	color:red;
}
.header-item>span:hover {
	cursor: pointer;
}
.content-container {
	flex: 1;
}


