
.home-container {
	display: flex;
	flex-direction: row;
	height: 100%;
}
.home-container > div.left {
	width: 300px;
	height: 100%;
	background-color: #a5a5a7;
}
div.left > h4 {
	margin-left: 8px;
}
div.left > ul {
	list-style: none;
	padding-left: 8px;
	cursor: pointer;
}
div.left > ul > li.active {
	color:darkblue;
}
.home-container > div.right {
	height: 100%;
	flex: 1;
}
