
.home-container {
	display: flex;
	flex-direction: row;
	height: 100%;
}
.home-container > div.left {
	width: 300px;
	height: 100%;
	background-color: #a5a5a7;
}
div.left > h4 {
	margin-left: 8px;
}
div.left > ul {
	list-style: none;
	padding-left: 8px;
	cursor: pointer;
}
div.left > ul > li {
	margin-bottom: 8px;
}
div.left > ul > li:before {
	content: "\A0";
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background-color: black;
	display: inline-block;
	line-height: 8px;
	margin-right: 8px;
}
div.left > ul > li.active {
	color:darkblue;
}
.home-container > div.right {
	height: 100%;
	flex: 1;
}
